const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'http://myz.free.idcfengye.com/api111',
		// baseUrl: 'http://ggkt2.vipgz1.91tunnel.com/api',
		// baseUrl: 'http://127.0.0.1::8201/api',
		loadingText: '...', // loading
		loadingTime: 800, // ms
		loadingMask: true, // loading
	});

	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		config.header.token = uni.getStorageSync('token')

		return config;
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		if (res.code == 200) {
			return res.data;
		} else if (res.code == 208) {
			// token
			uni.reLaunch({
				url: '/pages/login/login'
			})
			return false;
		} else {
			uni.showToast({
				title: res.message,
				icon: 'none'
			})
			return false;
		}
	}
}

export default {
	install
}