import {
	BAIDU_MAP_WEB_AK
} from './const.js';
const get_baidu_map_address =
	`https://api.map.baidu.com/reverse_geocoding/v3/?output=json&ak=${BAIDU_MAP_WEB_AK}&coordtype=wgs84ll&location=` // 
const get_home_index = 'home/index' // 
const get_sys_region_find_all_list = '/sys/region/findAllList' // 
const get_search_leader = '/search/leader' // 
const get_select_leader = 'user/leader/auth/selectLeader' // 
const get_categories = '/home/category' // 
const get_search_sku = '/search/sku' // 
const get_home_item = '/home/item' // 
const get_add_to_cart = '/cart/addToCart' // 
const get_cart_list = '/cart/cartList' // 
const get_check_cart = '/cart/checkCart' // 
const delete_cart = '/cart/deleteCart' // 
const get_activity_cart_list = '/cart/activityCartList' // 
const get_check_all_cart = '/cart/checkAllCart' // /
const post_batch_check_cart = '/cart/batchCheckCart' // /
const get_find_all_sec_kill_time_list = '/activity/seckill/findAllSeckillTimeList' // 
const get_find_sec_kill_sku_list = '/activity/seckill/findSeckillSkuList/' // sku
const get_coupon_info = '/activity/auth/getCouponInfo/' // 
const get_confirm_order = '/order/auth/confirmOrder' // 
const post_submit_order = '/order/auth/submitOrder' // 
const get_order_info = '/order/auth/getOrderInfoById' // 
const get_wx_login = '/user/weixin/wxLogin' // 
const post_update_user = '/user/weixin/auth/updateUser' // 
const get_weixin_payment = '/payment/weixin/createJsapi/' // 
const get_find_user_order = '/order/auth/findUserOrderPage' // 
const get_order_status = '/payment/weixin/queryPayStatus' // 

const install = (Vue, vm) => {
	const limit = 10;
	const page = 1

	// 
	const getHomeIndex = () => vm.$u.get(get_home_index);
	/*---------------------------------------------------------
		
	---------------------------------------------------------*/
	// 
	const getBaiduMapAddress = (o) => vm.$u.get(get_baidu_map_address + `${o.latitude},${o.longitude}`)
	// 
	const getSysRegionFindAllList = () => vm.$u.get(get_sys_region_find_all_list, {
		showLoading: false
	});
	// 
	const getSearchLeader = (o) => vm.$u.get(get_search_leader + `/${o.page}/${o.limit}`, {
		...o,
		showLoading: false
	})
	// 
	const getSelectLeader = (o) => vm.$u.get(get_select_leader + `/${o.leaderId}`, {
		showLoading: false
	})
	/*---------------------------------------------------------
		
	---------------------------------------------------------*/
	// 
	const getCategories = () => vm.$u.get(get_categories, {
		showLoading: false
	});
	// 
	const getSearchSku = (o) => vm.$u.get(get_search_sku +
		`/${o.page || page}/${o.limit || limit}`, {
			...o,
			limit: o.limit || limit
		});
	// 
	const getHomeItem = (o) => vm.$u.get(get_home_item + `/${o.skuId}`);
	/*---------------------------------------------------------
		
	---------------------------------------------------------*/
	// 
	const getFindAllSeckillTimeList = () => vm.$u.get(get_find_all_sec_kill_time_list);
	// sku
	const getFindSeckillSkuList = (o) => vm.$u.get(get_find_sec_kill_sku_list + `/${o.timeName}`);
	/*---------------------------------------------------------
		
	---------------------------------------------------------*/
	// 
	const getAddToCart = (o) => vm.$u.get(get_add_to_cart + `/${o.skuId}/${o.skuNum}`, {
		showLoading: false
	});
	// 
	const getCartList = () => vm.$u.get(get_cart_list);
	// 
	const getCheckCart = (o) => vm.$u.get(get_check_cart + `/${o.skuId}/${o.isChecked}`, {
		showLoading: false
	});
	// 
	const deleteCart = (skuId) => vm.$u.delete(delete_cart + `/${skuId}`, {
		showLoading: false
	});
	// 
	const getActivityCartList = (o) => vm.$u.get(get_activity_cart_list, {
		showLoading: o.showLoading ? o.showLoading : false
	});
	// /
	const getCheckAllCart = (o) => vm.$u.get(get_check_all_cart + `/${o.isChecked}`, {
		showLoading: o.showLoading ? o.showLoading : false
	});
	// /
	const postBatchCheckCart = (o) => vm.$u.post(post_batch_check_cart + `/${o.isChecked}`, o.skuIdList);
	// 
	const getCouponInfo = (o) => vm.$u.get(get_coupon_info + `/${o.id}`)
	// 
	const getConfirmOrder = () => vm.$u.get(get_confirm_order)
	// 
	const postSubmitOrder = (o) => vm.$u.post(post_submit_order, o)
	// 
	const getOrderInfo = (o) => vm.$u.get(get_order_info + `/${o.orderId}`)
	// 
	const getWxPayment = (o) => vm.$u.get(get_weixin_payment + `/${o.orderNo}`)
	
	// 
	const getOrderStatus = (o) => vm.$u.get(get_order_status + `/${o.orderNo}`)
	
	/*---------------------------------------------------------
		
	---------------------------------------------------------*/
	// 
	const getWxLogin = (o) => vm.$u.get(get_wx_login + `/${o.code}`, {
		showLoading: false
	})
	// 
	const postUpdateUser = (o) => vm.$u.post(post_update_user, {
		...o,
		showLoading: false
	})
	// 
	const getFindUserOrder = (o) => vm.$u.get(get_find_user_order + `/${o.page}/${o.limit}`, {
		...o
	})

	vm.$u.api = {
		getHomeIndex,
		getSysRegionFindAllList,
		getSearchLeader,
		getSelectLeader,
		getCategories,
		getSearchSku,
		getHomeItem,
		getAddToCart,
		getCartList,
		getCheckCart,
		deleteCart,
		getActivityCartList,
		getCheckAllCart,
		postBatchCheckCart,
		getBaiduMapAddress,
		getFindAllSeckillTimeList,
		getFindSeckillSkuList,
		getCouponInfo,
		getConfirmOrder,
		postSubmitOrder,
		getOrderInfo,
		getWxPayment,
		getWxLogin,
		postUpdateUser,
		getFindUserOrder,
		getOrderStatus
	};
}

export default {
	install
}